* {
    box-sizing: border-box;
}

.App {
    margin: 0 auto;
    box-sizing: border-box;
    background-color: #284652;
    height: 100vh;
    /*padding-top: 5%;*/
}

.error {
    color: orangered;
}

.container {
    max-width: 350px;
    width: 100%;
    margin: 0 auto;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #DBE9EE;
    border: 4px solid #93ACCA;
    border-radius: 10px;
}

.inner {
    max-width: 300px;
    margin: 0 auto;
}

.setting-container {
    width: 100%;
    /*height: auto;*/
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background-color: #DBE9EE;
    border: 4px solid #93ACCA;
    border-radius: 5px;
    margin: 20px 0;
}

.counter {
    width: 100%;
    height: auto;
    display: inline-block;
    /*justify-content: center;*/
    /*align-items: center;*/
    background-color: #DBE9EE;
    border: 4px solid #93ACCA;
    border-radius: 5px;
    margin: 20px 0;
}

.wrapper {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.set-btn-wrapper{
    width: 85%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    border-radius: 5px;
}

.set-btn-wrapper button {
    /*margin: 0;*/
}

.counterContainer {
    display: flex;
    justify-content: center;
    align-items: center;
}

.sectionName {
    background-color: #93ACCA;
    padding: 5px 15px;
    font-size: 22px;
    border-radius: 0 0 8px 8px;
    color: #394b56;
    margin-bottom: 15px;
    text-align: center;
}




